@import url(https://fonts.googleapis.com/css?family=Raleway);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fff9e6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.Login {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: column;
  position: absolute;
  height: 100vh;
  width: 50vw;
}

.Login input, .Login button {
  margin: 10px;
  font-size: 23px !important;
}

.input-box {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: row;
}

.error-msg {
  color: red;
  font-size: 14px;
  padding-top: 52px;
  position: absolute;
}

.border-red {
  border: 2px solid red;
}

.App {
    font-family: 'Raleway', sans-serif;
    font-size: 13px;
    display: flex;
    justify-content: center;
    margin-top: 5vh;
    line-height: 1.5;
}

@media (min-width: 992px) {
    .App {
        font-size: 20px;
    }
}

.Project {
  text-align: center;
  width: 50%;
}

.project-title {
  font-weight: 900;
  color: gray;
}

.details p:first-child {
  font-weight: 900;
}

.project-desc p:not(:first-child) {
  text-align: left;
  display: flex;
  justify-content: center;
}

.home {
  text-align: center;
}

p a {
  display: contents;
}

a:visited {
  color: inherit;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

a:hover, button:hover {
  cursor: pointer;
  font-weight: 700;
  font-size: 19px;
}

.navigation {
  max-width: 17vw;
  font-weight: 800;
  font-size: 17px;
  opacity: 0.8;
  bottom: 5vh;
  left: 5vw;
  position: fixed;
  background-color: white;
  border-radius: 5px;
  box-shadow: -1px 1px 5px rgba(0,0,0,0.2);
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.navigation:hover {
  opacity: 1;
}

.navi-title {
  color: #222222;
}

ul {
  list-style: none;
  padding-left: 20px;
}

button {
  background-color: pink;
  padding-left: 25px;
  text-indent: -15px;
  text-align: left;
  word-wrap: break-word;
  font-family: 'Raleway', sans-serif;
  font-size: 15px;
  border: none;
  line-height: 2;
  text-decoration: underline;
}

.modal {
    visibility: hidden;
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: black;
    background-color: rgba(0, 0, 0, 0.8);
}

.modal img {
    max-width: 99vw;
}

@media (min-width: 992px) {
    .modal img {
        max-width: 50vw;
        max-height: 80vh;
    }
}

@media (min-width: 992px) .image img {
    max-height: 40vh;
}

.image img {
    cursor: pointer;
    max-height: 27vh;
    text-align: center;
    max-width: 90%;
}

.image img:hover {
  border: solid 2px black;
}

figcaption {
  font-size: 14px;
  font-style: italic;
}

